.header {
  max-width: 1200px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 50px;

  .nav_logo {
    height: 40px;
    width: 40px;
  }

  .nav {
    &__menu {
      display: flex;
      justify-content: left;
      list-style: none;
      padding: 0;
      margin: 0;
    }

    &__menu-li {
      margin-right: 20px;

      &:last-child {
        margin-right: 0;
      }
    }

    &__link {
      display: block;
      height: 60px;
      line-height: 60px;
      font-family: inherit;
      font-style: normal;
      font-weight: 500;
      font-size: 12px;
      color: $color-dark-blue;
      text-transform: uppercase;
      cursor: pointer;

      &:hover {
        color: #00acdc;
      }
    }

    .is-active {
      position: relative;
      color: #00acdc;

      &::after {
        display: block;
        content: "";
        position: absolute;
        bottom: 0;
        left: 0;
        height: 4px;
        width: 100%;
        background-color: #00acdc;
        border-radius: 4px;
      }
    }
  }
}
